import { Vector3 } from '../../Utility/Vector3';

import { VAO } from '../../WebGL/VAO';
import { Model } from '../../WebGL/Model';

import { NGL3dApp } from '../..';

import { Shader } from '../../WebGL/Shader';
import { GeometryBase } from '../Core/Base';

class ShaderQuad extends Shader {

    uPointSize: WebGLUniformLocation | null;
    uAngle: WebGLUniformLocation | null;

    pMatrix: Float32Array;
    constructor(gl: WebGL2RenderingContext, vertSrc: string, fragSrc: string, pMatrix: Float32Array) {
        super(gl, vertSrc, fragSrc);
        this.pMatrix = pMatrix;

        // Our shader uses custom uniforms, this is the time to get its location for future use.
        if (this.program) {
            gl.useProgram(this.program);
            this.setPerspective(pMatrix);

            let uColor = gl.getUniformLocation(this.program, 'uColor');
            gl.uniform3fv(uColor, new Float32Array([0.8, 0.8, 0.8, 1, 0, 0, 0, 1, 0, 0, 0, 1]));

            // this.uPointSize = gl.getUniformLocation(this.program, 'uPointSize');
            // this.uAngle = gl.getUniformLocation(this.program, 'uAngle');

            // gl.useProgram(this.program);
            gl.useProgram(null); // Done setting up shader
        }
    }
    // Simple function that passes in Angle and Pointsize uniform data to the shader program.
    set(size: number, angle: number) {
        // this.gl.useProgram(null);
        // this.gl.uniform1f(this.uAngle, angle);
        // this.gl.uniform1f(this.uPointSize, size);
        return this;
    }
}

export class Quad extends GeometryBase {
    v = `#version 300 es

        in vec3 a_position;	// Standard position data.
        in vec2 a_uv;

        layout(location=4) in float a_color;

        uniform mat4 uPMatrix;
        uniform mat4 uMVMatrix;
        uniform mat4 uCameraMatrix;

        uniform vec3 uColor[4];

        out vec2 uv;
        out lowp vec4 color;

        void main(void) {
            uv = a_uv;
            // uv = vec2(0.5, 1);

            color = vec4(uColor[ int(a_color) ], 1.0);

            gl_Position = uPMatrix * uCameraMatrix * uMVMatrix * vec4(a_position, 1.0);
        }
    `;

    f = `#version 300 es

    precision mediump float;

    in vec2 uv;
    in vec4 color;

    out vec4 finalColor;

    void main(void) {

        //Square Border
        float c = (uv.x <= 0.1 || uv.x >=0.9 || uv.y <= 0.1 || uv.y >= 0.9)? 0.0 : 1.0;

        // finalColor = vec4(1.0, uv.x, uv.y ,1.0-c);
        // finalColor = vec4(color.xyz, 1.0 -c);

        // //Circle
        // //  /*
        vec2 delta = uv - vec2(0.5,0.5); //delta position from center;
        float dist = 0.5 - sqrt(delta.x*delta.x + delta.y*delta.y);

        float border = 0.01;
        float a = 0.0;
        if(dist > border) a = 1.0;
        else if(dist > 0.0) a = dist / border;

        finalColor = vec4(0.0,0.0,0.0, a);
        // //  */
    }
    `;
    app: NGL3dApp;
    vao: VAO;
    shader: ShaderQuad;
    model: Model;

    size: number = 20.5;
    rotation: number = 0.0;

    constructor(app: NGL3dApp) {
        super(app.scene);
        this.app = app;
        let aVert = [-0.5, 0.5, 0, -0.5, -0.5, 0, 0.5, -0.5, 0, 0.5, 0.5, 0];
        // 0,  0, 0,  1, 1,   1  ,1,  0
        let aUV = [0., 0., 0., 1., 1., 1., 1., 0.];
        let aIndex = [0, 1, 2, 2, 3, 0];

        this.shader = new ShaderQuad(this.app.canvas.gl, this.v, this.f, this.app.camera.projectionMatrix);
        this.vao = new VAO(app.canvas.gl, 'Quad', aVert, aIndex, undefined, aUV);
        this.vao.noCulling = true;
        this.vao.doBlending = true;

        this.model = new Model(this.vao);

    }
    render() {

        this.model.transform.position = new Vector3(0, 0, this.model.transform.position.z + -0.005);

        this.shader.activate();
        this.shader.setPerspective(this.shader.pMatrix);
        // this.shader.set(size,  this.rotation);
        // this.shader.activate().setCameraMatrix(this.app.camera.viewMatrix).renderModel(this.model.preRender());
        this.shader.setCameraMatrix(this.app.camera.viewMatrix);
        this.shader.renderModel(this.model.preRender());

    }
}